ture> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <ul><li>- 術後當日，皮膚泛紅或短暫的輕微如日曬般刺熱感，約持續一小時，可局部冰敷鎮靜皮膚，此為正常現象。</li> <li>- 術後至產生結痂以前，清洗臉部需使用煮沸的水或生理食鹽水，勿使用洗面乳，避免傷口感染。</li> <li>- 術後1～3天皮膚出現紅、熱、癢、緊繃及腫脹感，表示較為乾燥需加強保濕次數， 並可搭配藥膏使用。</li> <li>- 治療後出現點狀痂皮屬正常，洗臉時請勿用力搓揉。治療7～10天後，痂皮會自行慢慢脫落、色素變淡。請勿自行剝除痂皮，否則容易留傷口及疤痕。</li> <li>- 術後3～6天傷口即會癒合。早晚可以清撥清水洗臉，若皮膚呈現為紅或有為刺痛感時，可以將藥膏塗抹於患處。</li> <li>- 術後7天，勿搓揉皮膚或去角質。當保濕使用夠量時，有些傷口可能不會形成痂皮，會直接開始生長新皮，呈現粉紅色外觀。</li> <li>- 術後三周內，暫停使用含A酸、果酸、水楊酸、美白的藥膏、保養品及含有顆粒狀的洗面乳。不可進行三溫暖、烤箱、果酸煥膚、去角質等保養。</li> <li>- 加強防曬，一個月內出門前30分鐘使用SPF30～50係數之防曬乳液，烈陽下應撐傘和戴帽子、口罩加強防護，並加強保濕。</li> <li>- 減少食用光性食物，例如：九層塔、胡蘿蔔、芹菜、香菜、木瓜、檸檬、橘子、韭菜等。並可搭配醫師建議之美容保養療程，例如：保濕玻尿酸超聲波導入、美白針等，加強雷射療效。</li> <li><h4>本療程的效果可能因個人體質的不同而有差異，請務必與醫師溝通諮詢</h4></li></ul></div>")],2)}
